.heading{
  color: purple;
  font-size: 44px;
  font-family: 'Times New Roman', Times, serif;
  /* text-align: center; */
}

.align-label{
  display: inline-block;
  width: 250px;
  font-size: 22px;
  font-size: x-large;
}

.align-input{
  font-size: 22px;
  width: 300px;
  padding: 12px;
  border-radius: 6px;
  /* font-size:medium; */
  /* font-style: italic; */
  background-color: whitesmoke;
}

.space{
  margin: 15px;
  /* text-align: center; */
}

.button{
  background-color: purple;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 22px;
  color: white;
  border-radius: 6px;
  width: 200px;
  margin: 30px; 
}

.font{
  font-size: 22px;
}

.btn{
  color: white;
  padding: 8px;
  margin: 15px;
  font-size: 22px;
  border-color: blue;
  border-radius: 4px;
  background-color: blue;
}

.sub-heading{
  color: blue;
  font-size: 35px;
  margin: 15px;
}

.wrapper{
  height: 100vh;
  box-sizing: border-box;
}

.header{
  height: 10%;
  border: 2px solid red;
  box-sizing: border-box;
}

.container{
  display: flex;
  height: 90%;
  border: 2px solid red;
  box-sizing: border-box;
  flex-direction: row;
}

.sidebar{
  width: 20%;
  border: 2px solid red;
}

.main-content{
  width: 80%;
  border: 2px solid red;
}

.table-border{
  border: 2px solid black;
  border-collapse: collapse;
  text-align: center;
}

.t-width{
  width: 100%;
}

.profile-pic{
  border-radius: 75px;
    width: 60px;
}